
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import hosCommDetail from "./hosCommDetail";
export default {
components: {
hosCommDetail,
Cell,
Group,
CellBox
},
data() {
return {
title: "",
createDate: "",
context: ""
};
},
async created() {
let list = await this.getHosInfo("005");

this.items = list.map(item => {
item.createDate = dateFormat(
new Date(item.createDate),
"YYYY-MM-DD HH:mm:ss"
);

return item;
});
this.title = this.items[0].title;
this.createDate = this.items[0].createDate;
this.context = this.items[0].contents;
console.log('content',this.items,this.context);
},
methods: {
async getHosInfo(code) {
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getL2002 } = require("../../static/wxGetData");
let res = await getL2002(code).catch(error => {
throw new Error(error.retMsg);
});

return res;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
}
};



