
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Icon from 'vux/src/components/icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Box from 'vux/src/components/box/index.vue'

import { getA002 } from "../../static/wxGetData";

export default {
components: {
XButton,
Group,
Cell,
Grid,
GridItem,
XInput,
Selector,
GroupTitle,
Checker,
CheckerItem,
Toast,
Icon,
Flexbox,
FlexboxItem,
Box
},
data() {
return {
hosUser: {},
certTypeList: [
{ key: "01", value: "身份证" },
{ key: "02", value: "护照" },
{ key: "03", value: "军官证" },
{ key: "04", value: "其他" }
],
certType: "01",
userInfo: { name: "", certNo: "", phone: "", hosCardNo: "" }
};
},
methods: {
async SaveRegistInfo() {
let that = this;






try {
let userBase = JSON.parse(localStorage.getItem("useBaseInfo"));
let reqData = this.userInfo;
reqData.openid = this.$store.state.openid; 

reqData.nickname = userBase.nickname;
reqData.province = userBase.province;
reqData.city = userBase.city;
reqData.headimgurl = userBase.headimgurl;
reqData.country = userBase.country;
if (this.hosUser.patientId == "") {
throw new Error("请扫描二维码");
}
if (reqData.phone == "") {
throw new Error("请填写联系电话");
}
if (reqData.name == "") {
throw new Error("请填写真实姓名");
}
if (this.hosUser.name != reqData.name) {
throw new Error("您的填写的姓名与本院系统不匹配");
}
if (this.hosUser.phone != reqData.phone) {
throw new Error("您的填写的联系电话与本院系统不匹配");
}
if (this.hosUser.certNo != reqData.certNo) {
throw new Error("您的填写的证件号码与本院系统不匹配");
}

reqData.patientId = this.hosUser.patientId;
reqData.hosCardNo = this.hosUser.hosCardNo;
reqData.hosCardType = this.hosUser.hosCardType;
reqData.hosCardFrom = this.hosUser.hosCardFrom;
reqData.sex = this.hosUser.sex;

this.$vux.loading.show({ text: "Loading" }); 
const { getL0005 } = require("../../static/wxGetData");
let res = await getL0005(reqData).catch(error => {
throw new Error(error.retMsg);
});
this.$vux.loading.hide(); 

if (this.$route.name == "sickBind") {
that.$router.replace({
name: "sickList",
params: { msg: "成功", transResult: "" }
});
} else {
let toPath = localStorage.getItem("toPath");
that.$router.push(toPath);
}
} catch (error) {

this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async onlineRegist() {
this.$router.replace({
name: "sickCreate",
params: {}
});
},
async getHosUserByCard(cardNo) {

try {
let reqData = {};
reqData.hosCardNo = cardNo;
reqData.openid = this.$store.state.openid;
this.$vux.loading.show({ text: "Loading" }); 
const { getH1002 } = require("../../static/wxGetData");
let res = await getH1002(reqData).catch(error => {
throw new Error(error.retMsg);
});
this.$vux.loading.hide(); 
this.hosUser = res.data;
this.hosUser.hosCardNo = cardNo;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async onScan() {
const _this = this;
let wx = require("weixin-js-sdk");
const { getL0004 } = require("../../static/wxGetData");
let thisUrl = location.href.split("#")[0];
console.log("thisUrl", thisUrl);
let response = await getL0004({ url: thisUrl });

let res = response;

let options = {
debug: false,
appId: res.appId,
timestamp: res.timeStamp,
nonceStr: res.nonce_str,
signature: res.signature,
jsApiList: ["scanQRCode"]
};

wx.config(options);

wx.error(function(res) {
this.$vux.alert.show({ title: "提示", content: res.errMsg });
});
wx.ready(function() {
wx.checkJsApi({
jsApiList: ["scanQRCode"],
success: function(res) {}
});

wx.scanQRCode({
needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
success: function(res) {
_this.getHosUserByCard(res.resultStr); 
}
});
});
}
},
async created() {
let userinfo = JSON.parse(localStorage.getItem("userInfo"));; 
if (userinfo != null) {
this.certType = userinfo.certType;
} else {
this.certType = "01";
}
}
};



