
import trim from 'vux/src/tools/string/trim'
import Box from 'vux/src/components/box/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { stringify } from "querystring";
import inbaseinfo from "../common/inbaseinfo";
import selectSick from "../common/selectSick";
export default {
data() {
return {
rechargeAmount: "",

inHosInfo: {
inHosPatientName: "",
sex: "",
certNo: "",
certyType: "",
inHosNo: "",
deptName: "",
bed: "",
prePayTotalAmount: "0",
totalFee: "0",
inHosDate: ""
},
isAmount: function(value) {
return {
valid: value.length > 0,
msg: "预交的金额有误!"
};
}
};
},
components: {
selectSick,
Box,
CellBox,
GroupTitle,
Group,
Flexbox,
FlexboxItem,
Divider,
XInput,
CellFormPreview,
XButton,
dateFormat,
inbaseinfo
},
vuex: {},
methods: {
onFocus() {
this.timer = setInterval(() => {
document.body.scrollTop = document.body.scrollHeight;
}, 100);
},
onBlur() {
clearInterval(this.timer);
document.body.scrollTop = document.body.scrollHeight;

},
onGoConfirm() {
if (this.inHosInfo.inHosPatientName == "") {
this.$vux.toast.show({ text: "请选择就诊人", type: "text" });
return;
}
if (trim(this.$refs.amount.value.toString()) === "") {
this.$vux.toast.show({ text: "请输入金额", type: "text" });
return;
}
this.$router.replace({
name: "inPayConfirm",
params: {
rechargeAmount: this.rechargeAmount,
inHosInfo: this.inHosInfo
}
});
}
},
async mounted() {},
async created() {
this.inHosInfo = this.$store.state.inHosInfo;
},

computed: {},
watch: {}
};



