
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import selectSick from "../common/selectSick";
import myUtils from "../../utils/myUtils";
export default {
data() {
return {
isExist: true,
index: 0,



itemList: []
};
},
components: {
selectSick,
Cell,
CellBox,
CheckIcon,
Checklist,
Group,
XButton,
FormPreview,
Tab,
TabItem,
Swiper,
SwiperItem,
dateFormat
},
vuex: {},
methods: {
changeUserInfo() {
let dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
let dateEnd = this.dateUtil.getToday("yyyyMMdd");
this.getUnPay(dateStart, dateEnd);
},
async getUnPay(dateStart, dateEnd) {
try {
let req = {};
let user = JSON.parse(localStorage.getItem("userInfo"));
req.hosCardNo = user.hosCardNo;
req.hosCardType = user.hosCardType;
req.hosCardFrom = user.hosCardFrom;
req.name = user.name;


req.dateStart = dateStart;
req.dateEnd = dateEnd;
this.$vux.loading.show({ text: "Loading" }); 
const { getH1019 } = require("../../static/wxGetData");
let res = await getH1019(req).catch(error => {
throw new Error(error.retMsg);
});
this.itemList = this.statistics(res);
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
statistics(list) {
list = list.sort((a, b) => {
return b.doctor.localeCompare(a.doctor, "zh");
});
console.log("list =", list);
let newList = list.map((item, idx) => {
item.prescriptionDate = item.prescriptionTime.split(" ")[0];
item.checked = true;
item.isShowDetail = false;
return item;
});
let amount = 0;
let prescripts = "";
let pList = [];
let lastAcc = newList.reduce((acc, cur, idx, src) => {
let obj = cur;

let next = idx + 1;
amount += Number.parseFloat(cur.prescriptionAmount);
prescripts += cur.prescriptionNo + ",";
pList.push(cur);

if (
idx == src.length - 1 ||
cur.doctor != src[next].doctor ||
src[next].prescriptionDate != cur.prescriptionDate
) {
console.log("");
obj.Amount = amount;
obj.prescripts = prescripts;
obj.pList = pList;
acc.push(obj);
amount = 0;
prescripts = "";
pList = [];
}

return acc;
}, []);
return lastAcc;

}















},
created() {
let dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
let dateEnd = this.dateUtil.getToday("yyyyMMdd");
this.getUnPay(dateStart, dateEnd);
},
computed: {



}





















};



