
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Box from 'vux/src/components/box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import selectSick from "../common/selectSick";
export default {
components: {
selectSick,
Cell,
CellBox,
CellFormPreview,
Group,
Badge,
Icon,
Box,

PopupHeader,
Popup,
Radio
},
data() {
return {
isExist: false,
items: [],
show1: false,
tempUser: "",
wxUserList: [],
curUser: {}
};
},
methods: {
changeUserInfo() {



},
















async getType() {
try {
this.isExist = false;
this.$vux.loading.show({ text: "Loading" }); 
const { getH1003 } = require("../../static/wxGetData");
let res = await getH1003().catch(error => {
throw error;
});

this.items = res.filter((item, index) => {
return Number.parseInt(item.typeId) < 4 ? true : false;
});
this.isExist =
this.items == undefined || this.items.length == 0 ? true : false;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
async created() {
this.getType();
this.curUser =JSON.parse(localStorage.getItem("userInfo"));;
this.wxUserList = this.$store.state.userList.map(item => {
return { key: item.hosCardNo, value: item.name };
});
}
};



