
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Card from 'vux/src/components/card/index.vue'

import { finished } from 'stream';
export default {
components: {
Cell,
CellBox,
Msg,
Card
},

data() {
return {
description: "",
msgTitle: "",
transResult: {},
icon: "",
amount: 0,
buttons: [





]
};
},
methods: {},
async created() {
try {
this.$vux.loading.show({ text: "正在通讯中" }); 
let data = this.$route.params.payRetunData;

const { getH2001 } = require("../../static/wxGetData");
this.transResult = await getH2001(data);
this.description = ` 收据编号:${this.transResult.receiptNo}`;
this.msgTitle = `预交金充值成功,本次充值 <span style='color:red'>${Number.parseFloat(
this.transResult.amount
).toFixed(2)}</span>元`;
this.$vux.loading.hide(); 
} catch (err) {
this.$vux.loading.hide(); 
this.$router.replace({
name: "errorPage",
params: { errmsg: err.message }
});
}finally{
this.$vux.loading.hide(); 
}
}
};



