
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { getA002 } from "../../static/wxGetData";
import myUtils, { util } from "../../utils/myUtils";
export default {
components: {
XSwitch,
Selector,
Group,
Cell,
XButton,
XInput,
Toast
},
data() {
return {
isDefault: false,

selecedItem: "输入身份证号码",

certType: "01",
userInfo: {},
index: 0,
wechatCode: ""
};
},
methods: {
async SaveRegistInfo() {
let that = this;

try {
let reqData = this.userInfo;
/*微信平台获取的信息*/

if (reqData.certNo == "") {
this.$vux.toast.show({ text: "请填写身份证号", type: "text" });
return;
}
if (!myUtils.isIdCard(reqData.certNo)) {
this.$vux.toast.show({ text: "身份证号不正确", type: "text" });
return;
}
if (reqData.name == "") {
this.$vux.toast.show({ text: "请填写真实姓名", type: "text" });
return;
}
if (reqData.phone == "") {
this.$vux.toast.show({ text: "请填写联系电话", type: "text" });
return;
}

const { getH4005, L0006, H1006 } = require("../../static/wxGetData");

await getH4005(reqData).catch(error => {
throw new Error(error.retMsg);
});

await L0006(reqData).catch(error => {
throw new Error(error.retMsg);
});

const { querySickInfo, setDefault } = require("../../static/commReq");
await querySickInfo(this);

this.$vux.loading.hide(); 

let toPath = localStorage.getItem("toPath");
this.$router.push(toPath);
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.toast.show({ text: error.message, type: "text" });
} finally {
this.$vux.loading.hide(); 
}
},
getBirthdayFromIdCard(idCard) {
var birthday = "";
if (idCard != null && idCard != "") {
if (idCard.length == 15) {
birthday = "19" + idCard.substr(6, 6);
} else if (idCard.length == 18) {
birthday = idCard.substr(6, 8);
}

birthday = birthday.replace(/(.{4})(.{2})/, "$1-$2-");
}

return birthday;
}
},
created() {
this.userInfo = JSON.parse(localStorage.getItem("userInfo"));
}
};



