
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import { getL5003 } from "../../static/wxGetData";

export default {
components: {
Cell,
CellBox,
XInput,
Group,
XButton
},
data() {
return {
regInfo: {},
doctInfo: {},
user: {},
isHidePay: true,
showPhone:false,
list: [],
buttons1: [
{
style: "default",
text: "返回到首页",
link: "/"
},
{
style: "primary",
text: "确定支付"
}
]
};
},
methods: {
async onButtonClick() {
let self = this;
let respData = {};
if (this.user.phone == "") {
this.$vux.toast.show({ text: "请填写联系电话", type: "text" });
return;
}
this.isHidePay = false;
if (Number.parseFloat(this.doctInfo.registerTotalFee) <= 0) {
let postData = this.getRegistData();
postData.amount = 0;
postData.payStatus = "unpay";
self.$router.replace({
name: "appointResult",
params: { msg: "成功", payRetunData: postData }
});
return;
}

let data = await this.unifiedorder();
data.payModel = "04";
data.payStatus = "payed";
let wexinPay = require("../.././Libs/weixinPay");
wexinPay(
data,
async res => {
self.$router.replace({
name: "appointResult",
params: { msg: "支付成功", payRetunData: data }
});

},
res => {
self.$router.replace({
name: "errorPage",
params: { errmsg: result.responseHead.retMsg }
});

}
);
},
getRegistData() {
let model = {};
model.openid = this.user.openid;
model.hosCardNo = this.user.hosCardNo;
model.hosCardType = this.user.hosCardType; 
model.hosCardFrom = this.user.hosCardFrom; 
model.sex = this.user.sex == "1" ? "1" : "2";
model.name = this.user.name;
model.phone = this.user.phone;
model.certNo = this.user.certNo;
model.patientId = this.user.patientId;

model.deptId = this.regInfo.deptId; 
model.doctorId = this.regInfo.doctorId; 
model.typeId = this.$store.state.typeId;
model.deptName = this.doctInfo.deptName;
model.schedulingSequenceNum = this.regInfo.schedulingSequenceNum; 
model.registerTotalFee = this.doctInfo.registerTotalFee;
model.registerDate = this.doctInfo.curDate;
model.TranFlow = this.regInfo.TranFlow;
model.feeList = this.doctInfo.feeList;
model.total_fee = this.doctInfo.registerTotalFee;

model.busCode = "04";
model.payMode = "0"; 

model.body = "预约挂号";
return model;
},
async unifiedorder() {
let payUtil = {};
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getL5003 } = require("../../static/wxGetData");
let model = this.getRegistData();
let res = await getL5003(model).catch(error => {
throw new Error(error.retMsg);
});

payUtil = res;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
return payUtil;
}
},
created() {
this.user = JSON.parse(localStorage.getItem("userInfo"));
this.regInfo = this.$route.params.regInfo;
this.doctInfo = this.$store.state.doctorInfo;
if (this.user.phone == "") {
this.showPhone = true;
}








}
};



