
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


import selectSick from "../common/selectSick";

export default {
data() {
return {
isExist: false,
tabIndex: 0,
payedList: [],
unpayList: [],
list2: [
{
itemName: "已缴费",
itemList: []
},
{ itemName: "未缴费", itemList: [] }
],
selecedItem: "已缴费",
index: 0
};
},
components: {
XTable,
Tab,
TabItem,
Swiper,
SwiperItem,
XButton,
dateFormat,
selectSick
},
vuex: {},
methods: {
async changeUserInfo() {
if (this.tabIndex == 0) {
this.payedList = [];
this.getPayed();
} else {
this.unpayList = [];
this.getUnPay();
}
},
onIndexChange(tabIndex) {
if (tabIndex == 0) {
this.payedList = [];
this.getPayed();
} else {
this.unpayList = [];
this.getUnPay();
}
},
async getUnPay() {
try {
this.isExist = false;
let userInfo = JSON.parse(localStorage.getItem("userInfo"));
let hosCardNo = userInfo.hosCardNo;
let hosCardType = userInfo.hosCardType;
let hosCardFrom = userInfo.hosCardFrom;

let dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
let dateEnd = this.dateUtil.getToday("yyyyMMdd");
this.$vux.loading.show({ text: "Loading" }); 
const { getH1019 } = require("../../static/wxGetData");
let res = await getH1019(
hosCardNo,
hosCardType,
hosCardFrom,
dateStart,
dateEnd
).catch(error => {
throw new Error(error.retMsg);
});

this.unpayList = res;
this.isExist =
this.unpayList == undefined || this.unpayList.length == 0
? true
: false;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async getPayed() {
try {
this.isExist = false;
let userInfo = JSON.parse(localStorage.getItem("userInfo"));
let hosCardNo = userInfo.hosCardNo;
let hosCardType = userInfo.hosCardType;
let hosCardFrom = userInfo.hosCardFrom;

let dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
let dateEnd = this.dateUtil.getToday("yyyyMMdd");
console.log("dateStart", dateStart);
console.log("dateEnd", dateEnd);
this.$vux.loading.show({ text: "Loading" }); 
const { getH1033 } = require("../../static/wxGetData");
let res = await getH1033(
hosCardNo,
hosCardType,
hosCardFrom,
dateStart,
dateEnd
).catch(error => {
throw new Error(error.retMsg);
});

this.payedList = res;
this.isExist =
this.payedList == undefined || this.payedList.length == 0
? true
: false;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
created() {
this.getPayed();
},
computed: {
indexitem() {
return this.index;
}
},
watch: {
indexitem(oldData, newData) {
console.log(newData, oldData);
}
}
};



