
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
components: {
Cell,
Group,
CellBox
},
data() {
return {
items: [],

};
},
async created() {
let list = await this.getHosInfo("002");
this.items = list.map((item)=>{
item.createDate =  dateFormat(new Date( item.createDate), 'YYYY-MM-DD HH:mm:ss')
console.log('item',item.createDate);
return item;
})
console.log('items=',this.items);
},
methods: {

async getHosInfo(code) {
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getL2002 } = require("../../static/wxGetData");
let res = await getL2002(code).catch(error => {
throw new Error(error.retMsg);
});

return res;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
}
};



