
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { getH2003 } from "../../static/wxGetData";
import inbaseinfo from "../common/inbaseinfo";
import selectSick from "../common/selectSick";


export default {
data() {
return {
isExist: false,
list: [],

thisDate: "",
inHosInfo: {
inHosPatientName: "",
sex: "",
certNo: "",
certyType: "",
inHosNo: "",
deptName: "",
bed: "",
prePayTotalAmount: "0",
totalFee: "0",
inHosDate: "",
startDate: this.dateUtil.getYestoday("yyyy-MM-dd"),
endDate: this.dateUtil.getYestoday("yyyy-MM-dd")
}
};
},
components: {
Group,
selectSick,
Datetime,
XTable,
Tab,
TabItem,
Swiper,
SwiperItem,
XButton,
dateFormat,
inbaseinfo
},
vuex: {},
methods: {
async getInHosDailyFee() {
let that = this;
try {
this.isExist = false;

let curDate = this.dateUtil.Format(this.thisDate, "yyyyMMdd");
let inHosNo = this.$store.state.inHosInfo.inHosNo;
const { getH2002 } = require("../../static/wxGetData");
let res = await getH2002(inHosNo, curDate, curDate).catch(error => {
throw new Error(error.retMsg);
});

this.list = res;
this.isExist =
this.list == undefined || this.list.length == 0 ? true : false;
} catch (error) {
this.$vux.alert.show({
title: "",
content: error.message,
onHide() {

}
});
} finally {
this.$vux.loading.hide(); 
}
},
async onchange() {
console.log("onchange");
},
onCancel() {
console.log("onCancel");
},
async onConfirm() {
await this.getInHosDailyFee();
},
onHide() {
console.log("onHide");
},
async changeUserInfo() {
this.thisDate = this.dateUtil.getYestoday("yyyy-MM-dd");
await this.getInHosDailyFee();
}
},
async mounted() {},
async created() {
this.inHosInfo = this.$store.state.inHosInfo;
this.startDate =  this.inHosInfo.inHosDate;
this.endDate = this.dateUtil.getYestoday("yyyy-MM-dd");
this.thisDate = this.dateUtil.getYestoday("yyyy-MM-dd");
console.log('startDate endDate thisDate',this.startDate,this.endDate,this.thisDate);
await this.getInHosDailyFee();
},
computed: {},
watch: {}
};



