
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
components: {
Cell,
CellBox,
Msg,
Card
},
data() {
return {
description: " ",
msgTitle: "挂号成功",
transResult: {},
icon: "",
amount: 0,
buttons: [





]
};
},
methods: {},
async created() {
try {
this.$vux.loading.show({ text: "正在通讯中" }); 
let data = this.$route.params.payRetunData;
const { get } = require("../../static/wxGetData");
const { getH1008 } = require("../../static/wxGetData");
this.transResult = await getH1008(data).catch(error => {
throw new Error(error.retMsg);
});

this.amount = this.transResult.registerTotalFee;
this.$vux.loading.hide(); 
} catch (err) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "提示", content: err.message });
} finally {
this.$vux.loading.hide(); 
}
}
};



