
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
name: "selectSick",
components: { Swiper, SwiperItem },
data() {
return {

img_list: [
{
url: "javascript:",
img: "\\download\\article\\upload_d22a3b9c07147a319afc3c5d7459dae6.jpg"
},
{
url: "javascript:",
img: "\\download\\article\\upload_d22a3b9c07147a319afc3c5d7459dae6.jpg"
}
]
};
},
methods: {
async imgLoad() {

const { getH0001 } = require("../../static/wxGetData");
let reqData = {};
let imgList = await getH0001(reqData);


this.img_list = imgList.homeImg.map((one, index) => {
return {
url: one.linkUrl,
img: one.imgUrl.replace(/\\/g,'/')
};
});

}
},
async created() {
this.imgLoad();
}
};



