
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Icon from 'vux/src/components/icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Box from 'vux/src/components/box/index.vue'

import { getA002 } from "../../static/wxGetData";

export default {
components: {
XButton,
Group,
Cell,
Grid,
GridItem,
XInput,
Selector,
GroupTitle,
Checker,
CheckerItem,
Toast,
Icon,
Flexbox,
FlexboxItem,
Box
},
data() {
return {
cardInfo: {},
phone: "",
wechatCode: "",
idCardImg: ""
};
},
methods: {
async SaveRegistInfo() {
try {
let reqData = {};
/*微信平台获取的信息*/
let userBase = JSON.parse(localStorage.getItem("useBaseInfo")) ;
let weixinData = {};
weixinData.openid = localStorage.getItem("openid"); 

weixinData.nickname = userBase.nickname;
weixinData.province = userBase.province;
weixinData.city = userBase.city;
weixinData.headimgurl = userBase.headimgurl;
weixinData.country = userBase.country;

reqData.nation = this.cardInfo.nation;
reqData.openid = localStorage.getItem("openid");
reqData.certNo = this.cardInfo.id;
reqData.phone = this.phone;
reqData.name = this.cardInfo.name;

if (reqData.phone == "") {
this.$vux.toast.show({ text: "请填写联系电话", type: "text" });
return;
}

reqData.birth = this.getBirthdayFromIdCard(reqData.certNo);
reqData.wechatCode = this.wechatCode;
this.$vux.loading.show({ text: "Loading" }); 
let data = { cardInfo: reqData, wixin: weixinData };
const { getH1001 } = require("../../static/wxGetData");
let res = await getH1001(data).catch(error => {
this.$vux.toast.show({ text: error.retMsg, type: "text" });
return ;
});
this.$vux.loading.hide(); 

this.$router.replace({ name: "myInfo" });
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.toast.show({ text: error.message, type: "text" });
} finally {
this.$vux.loading.hide(); 
}
},
getBirthdayFromIdCard(idCard) {
var birthday = "";
if (idCard != null && idCard != "") {
if (idCard.length == 15) {
birthday = "19" + idCard.substr(6, 6);
} else if (idCard.length == 18) {
birthday = idCard.substr(6, 8);
}

birthday = birthday.replace(/(.{4})(.{2})/, "$1-$2-");
}

return birthday;
},

getLocalImgData(localId) {
let wx = require("weixin-js-sdk");
let _this = this;
wx.getLocalImgData({
localId: localId, // 图片的localID
success: function(res) {
var localData = res.localData; 
if (window.__wxjs_is_wkwebview) {

localData = localData.replace("jgp", "jpeg"); 
} else {
localData = "data:image/jpeg;base64," + localData; 
}
_this.idCardImg = localData;
console.log(" this.idCardImg", _this.idCardImg);

}
});
}
},
async created() {
this.cardInfo = this.$route.params.cardInfo;
this.wechatCode = this.$route.params.wechatCode;
this.getLocalImgData(this.$route.params.localId);
}
};



