export default {
  data() {
    return {};
  },
  methods: {
    getUrlKey(name) {
      return (
        decodeURIComponent(
          (new RegExp("[?|&]" + name + "=" + "([^&;]+?)(&|#|;|$)").exec(
            location.href
          ) || [, ""])[1].replace(/\+/g, "%20")
        ) || null
      );
    },

    getCodeApi(state) {
      //获取code
      let urlNow = encodeURIComponent(window.location.href);
      let scope = "snsapi_base"; //snsapi_userinfo   //静默授权 用户无感知
      let appid = localStorage.getItem("appid");
      let url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${urlNow}&response_type=code&scope=${scope}&state=${state}#wechat_redirect`;
      window.location.replace(url);
    },
    gotoNextPage() {
      let nextPage = localStorage.getItem("nextPage");
      this.$router.replace({
        name: nextPage,
        params: {}
      });
    }
  },

  async created() {
    try {
      if (this.$store.state.openid == null || this.$store.state.openid == "") {
        const { getH0001, getH0002 } = require("../static/wxGetData");
        let reqData = {};
        let res = await getH0001(reqData);
        // if (
        //   localStorage.getItem("appid") == "" ||
        //   localStorage.getItem("appid") == null
        // ) {
        localStorage.setItem("appid", res.appid);
        // }
        let homeImgList = res.homeImg.map((item, index) => {
          return "\\" + item.imgUrl;
        });
        localStorage.setItem("homeImg", JSON.stringify(homeImgList));
        //   this.$store.commit("homeImg", homeImgList);

        let code = this.getUrlKey("code");
        if (code) {
          reqData.code = code;

          let res = await getH0002(reqData);
          this.$store.commit("openid", res.openid);
          localStorage.setItem("openid", res.openid);
          this.$store.commit("useBaseInfo", res);
          localStorage.setItem("useBaseInfo", JSON.stringify(res));
          this.gotoNextPage();
        } else {
          
          this.getCodeApi("123");
        }
      }
    } catch (err) {
    } finally {
    }
  }
};



