import Vue from 'vue'
import Router from 'vue-router'
import utils from '../utils/myUtils'


import routeConfig from './routeConfig'
Vue.use(Router)
let router = new Router({
  routes: routeConfig,
  //mode: "history",
})
router.beforeEach(async function (to, from, next) {
  // router.app.$store.commit("openid", "o8_qu5qEalvTvjFP29m6o_YlUvrE");
  // localStorage.setItem("openid", "o8_qu5qEalvTvjFP29m6o_YlUvrE");
  const {
    querySickInfo
  } = require("../static/commReq");


  window.document.title = to.meta.title;
  if (to.path == '/main' && to.query.nextPage != undefined) {
    localStorage.setItem('nextPage', to.query.nextPage)
  }
  if (to.meta.checkAuth == 1) {

    let ret = await querySickInfo(router.app); // await queryUserIsExist(router.app.$store.state.openid) //查询是否已经注册
  
    if (Array.isArray(ret) && ret.length > 0) {
       
      // if (ret) { //已经注册
     // let user = JSON.parse(localStorage.getItem("userInfo"))
      // if (user.phone == "") {
      //   next({
      //     path: "/modifyInfo/"
      //   });
      // } else {
        next();
     // }

    } else { //没有注册跳转到注册页面
      localStorage.setItem('toPath', to.fullPath)
      next({
        path: "/myInfo/"
      })
    }
  } else {
    if (to.path == '/sickCreate' && to.query.wechatCode == undefined) {
      let redirect_uri = encodeURIComponent('http://weixin.zysyy.com.cn/#/sickCreate');
      // let url = `https://health.tengmed.com/open/getHealthCardList?redirect_uri=${redirect_uri} `;
      let url = `https://health.tengmed.com/open/getUserCode?redirect_uri=${redirect_uri}`;

      window.location.replace(url);
    } else {
      next();
    }

  }



});
router.afterEach(async function (to, from, next) {


});
/**
 * 通过编号查询用户的openid是否已经存在，如果已经注册跳转病人注册界面
 */
async function queryUserIsExist(openid) {
  openid = utils.isNull(openid) ? localStorage.getItem("openid") : openid;
  const {
    getL5001
  } = require("../static/wxGetData");
  let res = await getL5001(openid).catch(error => {
    //code 到后台换取openid
    throw new Error(error.retMsg);
  });



  if (Array.isArray(res.UserData) && res.UserData.length > 0) {

    for (var item in res.UserData) {
      if (res.UserData[item].isDefault === 1) {
        router.app.$store.commit('userInfo', res.UserData[item]);
        break;
      }
    }
    router.app.$store.commit('userList', res.UserData);
    return true;

  } else {
    return false;
  }

}
export default router;



