import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 定义状态
  state: {
    userInfo: {}, //默认用户信息
    userList: [], //用户列表,注册多个哟用户
    token: '',
    openid: '',
    appid:'',
    typeId:'',
    deptId:'',
    deptName:'',
    doctorId:'',
    registerFee:'',
    homeImg:[],
    useBaseInfo:{},
    inHosInfo:{},
    doctorInfo:{},
  },
  mutations: {
    userInfo(state, userModel) {
      state.userInfo = userModel;
    },
    userList(state, userlist) {
      state.userList = userlist;
    },
    token(state, token) {
      state.token = token;
    },
    openid(state, openid) {
      state.openid = openid;
    },
    appid(state, appid) {
      state.appid = appid;
    },
    useBaseInfo(state, baseInfo) {
      state.useBaseInfo = baseInfo;
    },
    typeId(state, typeId) {
      state.typeId = typeId;
    },
    
    deptName(state, deptName) {
      state.deptName = deptName;
    },
    deptId(state, deptId) {
      state.deptId = deptId;
    },
    doctorId(state, doctorId) {
      state.doctorId = doctorId;
    },
    registerFee(state, registerFee) {
      state.registerFee = registerFee;
    },
    homeImg(state, homeImg) {
      state.homeImg = homeImg;
    },
    inHosInfo(state, inHosInfo) {
      state.inHosInfo = inHosInfo;
    },
    doctorInfo(state, doctorInfo) {
      state.doctorInfo = doctorInfo;
    },
    
  }
})

export default store



