

export default class myUtils {
    constructor() {

    }
    /**
     * 判断字符串是否为空
     * @param str
     * @returns {boolean}
     */
    static isNull(str) {
     
        return str == undefined || str == null || str.length === 0 || (str != undefined &&str.trim() === '');
    }
   /** 
     * js截取字符串，中英文都能用 
     * @param str：需要截取的字符串 
     * @param len: 需要截取的长度 
     */
     static cutstr(str, len) {
        var str_length = 0;
        var str_len = 0;
        let str_cut = new String();
        str_len = str.length;
        for (var i = 0; i < str_len; i++) {
           let a = str.charAt(i);
            str_length++;
            if (escape(a).length > 4) {
                //中文字符的长度经编码之后大于4  
                str_length++;
            }
            str_cut = str_cut.concat(a);
            if (str_length >= len) {
                str_cut = str_cut.concat("...");
                return str_cut;
            }
        }
        //如果给定字符串小于指定长度，则返回源字符串；  
        if (str_length < len) {
            return str;
        }
    }
    /**
     *
     * @desc  判断是否为身份证号
     * @param  {String|Number} str
     * @return {Boolean}
     */
    static isIdCard(str) {
        return /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(str)
    }
    static listIsExist(List,obj,objName){
        console.log('List,obj,objName===========', JSON.stringify(List) ,obj,objName);
        let ret = false;
        if (Array.isArray(List) && List.length > 0) {
            let findRet = List.find((item, index) => {
              return item[objName] == obj;
            });
            
    
            if (!myUtils.isNull(findRet)) {
              
                ret=true ;
            }
          }
          console.log('ret ===',ret);
          return ret ;
    }
    /**
     *
     * @desc   判断是否为手机号
     * @param  {String|Number} str
     * @return {Boolean}
     */
    static isPhoneNum(str) {
        return /^(0|86|17951)?(1[3-9][0-9])[0-9]{8}$/.test(str)
    }
    /**
     * param json 对象 转成 Url
     */
    static parseUrlParam = function (data) {
        try {
            var tempArr = [];
            for (var i in data) {
                var key = encodeURIComponent(i);
                var value = encodeURIComponent(data[i]);
                tempArr.push(key + '=' + value);
            }
            var urlParamsStr = tempArr.join('&');
            return urlParamsStr;
        } catch (err) {
            return '';
        }


    };
    /**
     *  将Url 转成Json 对象
     */
    static urlToJson = function () {
        var ret = {};
        window.location.search.substr(1).replace(/(\w+)=(\w+)/ig, function (a, b, c) { ret[b] = unescape(c); });
        return ret;
    };
    static getUrlKey(name) {
        return (
          decodeURIComponent(
            (new RegExp("[?|&]" + name + "=" + "([^&;]+?)(&|#|;|$)").exec(
              location.href
            ) || [, ""])[1].replace(/\+/g, "%20")
          ) || null
        );
      }
    static formatDateTime = function (date) {
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        var second = date.getSeconds();
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
    };
    static calcAge(status) {
        var aDate = new Date();
        var brith = Number(status.substr(0, 4));
        var thisYear = aDate.getFullYear();
        var age = thisYear - brith;
        return age;
    };
    static formatDate = function (date, mark = "") {
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;

        return y + mark + m + mark + d;
    };
    static stringToDate = function (datestr) {
        let y = datestr.substr(0, 4);
        let m = datestr.substr(4, 2);
        let d = datestr.substr(6, 2);
        let h = datestr.substr(8, 2);
        let minute = datestr.substr(10, 2);
        let second = datestr.substr(12, 2);
        return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
    };
    static formatTime = function (date, mark = "") {
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        var second = date.getSeconds();
        second = second < 10 ? ('0' + second) : second;
        return h + mark + minute + mark + second;
    };
    static getWeek = function (item) {
        let year = parseInt(item.substr(0, 4));
        let month = parseInt(item.substr(4, 2)) - 1;
        let day = parseInt(item.substr(6, 2));
        console.log('date=',year, month, day);
        let date = new Date(year, month, day);
        console.log('date=',date,item);
        let text = "";
        let week = date.getDay();
        switch (week) {
            case 0:
                text = "周日";
                break;
            case 1:
                text = "周一";
                break;
            case 2:
                text = "周二";
                break;
            case 3:
                text = "周三";
                break;
            case 4:
                text = "周四";
                break;
            case 5:
                text = "周五";
                break;
            case 6:
                text = "周六";
                break;
        }
        return text;
    };
    static randomNumber = function () {
        const now = new Date()
        let month = now.getMonth() + 1
        let day = now.getDate()
        let hour = now.getHours()
        let minutes = now.getMinutes()
        let seconds = now.getSeconds()
        let getMilliseconds = now.getMilliseconds()
        // month = this.setTimeDateFmt(month)
        // hour = this.setTimeDateFmt(hour)
        // minutes = this.setTimeDateFmt(minutes)
        // seconds = this.setTimeDateFmt(seconds)
        return now.getFullYear().toString() + month.toString() + day + hour + minutes + seconds + getMilliseconds.toString() + (Math.round(Math.random() * 89 + 100)).toString()
    }
    //多余显示省略号
    static wordlimitfunction(cname, wordlength) {
        var cname = document.getElementsByClassName(cname);
        for (var i = 0; i < cname.length; i++) {
            var nowLength = cname[i].innerHTML.length;
            if (nowLength > wordlength) {
                cname[i].innerHTML = cname[i].innerHTML.substr(0, wordlength) + '...';
            }
        }
    };
}



