
import { TMap } from "../../utils/tMap";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'


export default {
components: {
Group,
XTable,
Tab,
TabItem,
Swiper,
SwiperItem,
XButton,
dateFormat
},
data() {
return {
listTab: [{ itemName: "院外导航" }, { itemName: "实景地图" }],
selecedItem: "院外导航",
index: 0,
Tab,
TabItem,
Swiper,
SwiperItem
};
},
methods: {
async hosoutMap() {
console.log('dddddddddddddddddddddd');
let qq = await TMap("XMHBZ-CMACJ-2UWFK-FXTZL-YO2M2-QRFRJ");
let center = new qq.maps.LatLng(38.94302, 100.40166);
var map = new qq.maps.Map(document.getElementById("container"), {

center: center,
zoom: 14,
zoomControl: false
});
var marker = new qq.maps.Marker({
position: center,
map: map
});
},
async streetMap() {
let qq = await TMap("XMHBZ-CMACJ-2UWFK-FXTZL-YO2M2-QRFRJ");
var pano = new qq.maps.Panorama(document.getElementById("pcontainer"));

pano.setOptions({

disableMove: false,

disableCompass: true
});

pano.setPano("32071047130730154653500");

pano.setPov({
heading: 285,
pitch: 12
});

pano.setZoom(1);
}
},
async mounted() {
this.hosoutMap();
},
computed: {
indexitem() {
return this.index;
}
},
watch: {
indexitem(newData, oldData) {
console.log(newData)
if (newData == 1) {
this.streetMap();
} else {
this.hosoutMap();
}

}
}
};



