
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import myUtils from "../../utils/myUtils";
export default {
data() {
return {
isExist: true,
index: 0,
isShow: false,
amount: 0,
itemList: [],
detailList: [],
user: {}
};
},
components: {
Cell,
CellBox,
CheckIcon,
Checklist,
Group,
XButton,
FormPreview,
Tab,
Flexbox,
FlexboxItem,
TabItem,
Swiper,
SwiperItem,
dateFormat,
PopupHeader,
Popup,
XTable
},
vuex: {},
methods: {
isNull(str) {
return str == undefined || str == null || str.length === 0 || str.trim() === '';
},
calc() {
this.amount = this.itemList.reduce((acc, cur, idx, src) => {
if (cur.checked) {
acc += Number.parseFloat(cur.prescriptionAmount);
}
return acc;
}, 0);
},
payed() {

let nList = this.itemList.filter(item => {
return item.checked;
});
if (nList.length == 0) {
this.$vux.alert.show({ title: "", content: "请选择要支付的处方" });
return;
}

let pArr = nList.map((item, index) => {
return item.prescriptionNo;
});
this.callUnifiedorder(pArr.join("|"));
},
showDetail(item, index) {
this.getUnPayDetail(item.prescriptionNo, index);
},
async getUnPayDetail(prescriptionNo, index) {
try {
this.$vux.loading.show({ text: "Loading" }); 

let hosCardNo = this.user.hosCardNo;
let hosCardType = this.user.hosCardType;
let hosCardFrom = this.user.hosCardFrom;
const { getH1020 } = require("../../static/wxGetData");
let res = await getH1020(
hosCardNo,
hosCardType,
hosCardFrom,
prescriptionNo
).catch(error => {
throw new Error(error.retMsg);
});
this.detailList = res;
this.isShow = true;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async unifiedorder(prescriptionNo) {
let payUtil = {};
try {
this.$vux.loading.show({ text: "Loading" }); 

let model = {};
model.openid = this.user.openid; 
model.hosCardNo = this.user.hosCardNo;
model.hosCardType = this.user.hosCardType; 
model.hosCardFrom = this.user.hosCardFrom; 
model.patientId = this.user.patientId;
model.sex = this.user.sex == "1" ? "1" : "2";
model.phone = this.user.phone;
model.certNo = this.user.certNo;
model.prescriptionNos = prescriptionNo; 
model.prescriptionsAmount = this.amount; 
model.payAmount = this.amount; 


model.total_fee = this.amount; 
model.busCode = "02";
model.payMode = "0"; 
model.body = "处方缴费";

console.log("model", model);
const { getL5003 } = require("../../static/wxGetData");
let res = await getL5003(model).catch(error => {
throw new Error(error.retMsg);
});

payUtil = res;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
return payUtil;
},
async callUnifiedorder(prescriptionNos) {
try {
this.isHidePay = false;
let data = await this.unifiedorder(prescriptionNos);
data.model = "04";
let self = this;
let wexinPay = require("../.././Libs/weixinPay");


console.log('data====',data);
wexinPay(
data,
async res => {
self.$router.replace({
name: "outPayResult",
params: {
msg: "支付完成",
payRetunData: data,
pNo: prescriptionNos
}
});

},
res => {
self.$router.replace({
name: "errorPage",
params: { errmsg: result.responseHead.retMsg }
});

}
);
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
}
}
},
created() {
this.user = JSON.parse(localStorage.getItem("userInfo"));
this.itemList = this.$route.params.prescription.pList;
console.log("this.itemList ", this.itemList);
},
computed: {
itemList() {
return this.itemList;
}
},
watch: {
itemList: {
handler(newValue, oldValue) {
console.log(newValue);
this.calc();
},
deep: true
}
}
};



