
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


import { getL5003 } from "../../static/wxGetData";
import inbaseinfo from "../common/inbaseinfo";
export default {
components: {
Cell,
Box,
GroupTitle,
Group,
Flexbox,
FlexboxItem,
Divider,
CellBox,
XInput,
inbaseinfo,
XButton,
dateFormat
},
data() {
return {
rechargeAmount: "0",
isHidePay: true,
user: {},

inHosInfo: {
inHosPatientName: "",
sex: "",
certNo: "",
certyType: "",
inHosNo: "",
deptName: "",
bed: "",
prePayTotalAmount: "",
inHosDate: ""
}
};
},
methods: {
async unifiedorder() {
let payUtil = {};
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getL5003 } = require("../../static/wxGetData");
let model = {};

model.openid = this.$store.state.openid;
console.log(' this.inHosInfo', this.inHosInfo);
model.hosCardNo = this.inHosInfo.patientId;
model.hosCardType = ''; 
model.hosCardFrom = ''; 
model.name = this.inHosInfo.inHosPatientName;
model.sex = this.inHosInfo.sex == "01" ? "1" : "2";
model.phone ='';
model.certNo =  this.inHosInfo.certNo;
model.patientId = this.inHosInfo.patientId;
model.inHosNo = this.inHosInfo.inHosNo; 
model.rechargeAmount =Number( this.rechargeAmount).toFixed(2); 
model.total_fee = Number( this.rechargeAmount).toFixed(2);
model.busCode = "03";
model.payMode = "0"; 

model.body = "住院缴费";

let res = await getL5003(model).catch(error => {
throw new Error(error.retMsg);
});

payUtil = res;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
return payUtil;
},
async onPrePay() {
this.isHidePay = false;
let data = await this.unifiedorder();

let self = this;
let wexinPay = require("../.././Libs/weixinPay");
wexinPay(
data,
async res => {
self.$router.replace({
name: "inPayResult",
params: { msg: "支付成功", payRetunData: data }
});

},
res => {
self.$router.replace({
name: "errorPage",
params: { errmsg: result.responseHead.retMsg }
});

}
);
}
},
created() {

this.inHosInfo = this.$store.state.inHosInfo
this.user =   JSON.parse(localStorage.getItem("userInfo"));
this.rechargeAmount = this.$route.params.rechargeAmount;
this.rechargeAmount = Number.parseFloat(this.rechargeAmount).toFixed(2);

}
};



