
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

import myUtils from "../../utils/myUtils";


export default {
data() {
return {
isExist: false,

itemList: []
};
},
components: { XTable, Tab, TabItem, Swiper, SwiperItem, XButton,Box },
vuex: {},
methods: {
async getReportDetail(reportNo) {
try {
let user = JSON.parse(localStorage.getItem("userInfo"));;
this.$vux.loading.show({ text: "Loading" }); 
const { getH1027 } = require("../../static/wxGetData");
let res = await getH1027(
user.hosCardNo,
user.hosCardType,
reportNo
).catch(error => {
throw new Error(error.retMsg);
});
this.itemList =     res.map((item,index)=>{item.itemName =myUtils.cutstr(item.itemName,14);return item});
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
mounted() {},
created() {
let reportNo = this.$route.params.reportNo;
this.getReportDetail(reportNo);
},
computed: {
indexitem() {
return this.index;
}
},
watch: {
indexitem(oldData, newData) {}
}
};



