
import Group from 'vux/src/components/group/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
Group,
XButton,
Icon,
CellBox,
Flexbox,
FlexboxItem
},
data() {
return {

isDefault: false,
List: [],
user: {},

};
},
methods: {
async change(value) {
await this.setDefault(value);
},
async onUnBind(item) {
let that = this;
this.$vux.confirm.show({
title: "确定要解除该用户绑定吗?",


onConfirm: async () => {
console.log("item", item);
await that.SaveRegistInfo(item);
await that.querySickInfo();
}
});
},
async setDefault(reqData) {
let that = this;

try {
this.$vux.loading.show({ text: "Loading" }); 
const { getH0010 } = require("../../static/wxGetData");
let res = await getH0010(reqData).catch(error => {
throw new Error(error.retMsg);
});
this.List = await this.querySickInfo();
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async querySickInfo() {
let that = this;

try {
let reqData = {};
reqData.openid = this.$store.state.openid; 
this.$vux.loading.show({ text: "Loading" }); 
const { getH1006 } = require("../../static/wxGetData");
let res = await getH1006(reqData).catch(error => {
throw new Error(error.retMsg);
});

res.forEach(element => {
if (element.isDefault == "1") {
that.$store.commit("userInfo", element);
}
});
console.log('reqData====================================',reqData);
that.$store.commit("userList", res);

return res;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}, //保存解除绑定的信息
async SaveRegistInfo(reqData) {
let that = this;

try {
reqData.openid = this.user.openid;
this.$vux.loading.show({ text: "Loading" }); 
const { getH1000 } = require("../../static/wxGetData");
let res = await getH1000(reqData).catch(error => {
throw new Error(error.retMsg);
});
this.$vux.loading.hide(); 
this.$vux.alert.show({
title: "",
content: "解除绑定成功",
async onHide() {
that.List = await that.querySickInfo();
}
});
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
async created() {

this.user =JSON.parse(localStorage.getItem("userInfo"));;
this.List = await this.querySickInfo();
}
};



