
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Box from 'vux/src/components/box/index.vue'


import myUtils, { util } from "../../utils/myUtils";
import { awaitExpression } from "babel-types";
export default {
name: "queryInSick",
props: {
nextPage: {
type: String
}
},
components: {
Group,
XButton,
XInput,
Toast,
Box
},
data() {
return {
inHosNo: "",
inHosName: "",
inHosInfo: {
inHosPatientName: "",
sex: "",
certNo: "",
certyType: "",
inHosNo: "",
deptName: "",
bed: "",
prePayTotalAmount: "0",
totalFee: "0",
inHosDate: ""
}
};
},
methods: {
async getinHosInfo() {
if (this.inHosName.trim() == "") {
this.$vux.toast.show({ text: "请填写姓名", type: "text" });
return;
}
if (this.inHosNo.trim() == "") {
this.$vux.toast.show({ text: "请填写住院号", type: "text" });

return;
}

let that = this;
this.$vux.loading.show({ text: "正在通讯中" }); 
try {
const { getH2003, getH2005 } = require("../../static/wxGetData");
let h2005Res = await getH2005(this.inHosNo, "", "").catch(error => {
throw new Error(error.retMsg);
});
let res = await getH2003(h2005Res.inHosInfo.inHosNo).catch(error => {
throw new Error(error.retMsg);
});
let sex = "未知";
if (res.sex == "01") sex = "男";
if (res.sex == "02") sex = "女";
this.inHosInfo.inHosPatientName = res.inHosPatientName;
this.inHosInfo.sex = sex;
this.inHosInfo.certNo = res.certNo;
this.inHosInfo.certyType = res.certyType;
this.inHosInfo.inHosNo = h2005Res.inHosInfo.inHosNo;
this.inHosInfo.deptName = res.deptName;
this.inHosInfo.bed = res.bed;
this.inHosInfo.totalFee = res.totalFee;
this.inHosInfo.prePayTotalAmount = res.prePayTotalAmount;
this.inHosInfo.inHosDate = res.inHosDate;
this.inHosInfo.patientId = res.PatientId;
console.log("res", res);
if (this.inHosInfo.inHosPatientName != this.inHosName) {
throw new Error("就诊人姓名与本院登记不一致");
}
this.$vux.loading.hide(); 
this.$store.commit("inHosInfo", this.inHosInfo);
this.$router.replace({ name: this.nextPage });
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({
title: "",
content: error.message,
onHide() {

}
});
} finally {

}
}
},
created() {}
};



