/**获取支付页面测试 */
export function getL5010(amount) {


  let param = {
    requestHead: {
      busCode: 'L5010',
      busName: '获取支付页面测试'
    },
    data: {}
  };

  param.data.amount = amount;
  return callPost(param)

}

function callPost(params) {
  return new Promise((resolve, reject) => {
    console.log('basurl', baseUrl);
    httpReq.post(baseUrl, params).then((resp) => {
      // resp = resp.response;
      if (resp.responseHead.retCode === '00') {
        resolve(resp.data);
      } else {
        reject({
          retCode: resp.responseHead.retCode,
          retMsg: resp.responseHead.retMsg
        });
      }
    }).catch((err) => {
      reject({
        retCode: '01',
        retMsg: err.message
      });
    });

  });
}



