

//filter
if (!Array.prototype.filter){
    Array.prototype.filter = function(fun){
        if (this === void 0 || this === null)
          throw new TypeError();

        var t = Object(this);
        var len = t.length >>> 0;
        if (typeof fun !== "function")
          throw new TypeError();

        var res = [];
        var thisArg = arguments.length >= 2 ? arguments[1] : void 0;
        for (var i = 0; i < len; i++){
            if (i in t){
                var val = t[i];
                if (fun.call(thisArg, val, i, t))
                  res.push(val);
            }
        }
    return res;
  };
}

//map
if (!Array.prototype.map) {
    Array.prototype.map = function(callback, thisArg) {
        var T, A, k;
        if (this == null) {
            throw new TypeError(" this is null or not defined");
        }
        var O = Object(this);
        var len = O.length >>> 0;
        if (typeof callback !== "function") {
            throw new TypeError(callback + " is not a function");
        }
        if (thisArg) {
            T = thisArg;
        }
        A = new Array(len);
        k = 0;
        while(k < len) {
            var kValue, mappedValue;
            if (k in O) {
                kValue = O[ k ];
                mappedValue = callback.call(T, kValue, k, O);
                A[ k ] = mappedValue;
            }
            k++;
        }
        return A;
    };
}
String.prototype.Ttrim = function (c) {
    if (c == null || c == "") {
        var str = this.replace(/^s*/, '');
        var rg = /s/;
        var i = str.length;
        while (rg.test(str.charAt(--i)));
        return str.slice(0, i + 1);
    }
    else {
        var rg = new RegExp("^" + c + "*");
        var str = this.replace(rg, '');
        rg = new RegExp(c);
        var i = str.length;
        while (rg.test(str.charAt(--i)));
        return str.slice(0, i + 1);
    }
}
//去除字符串头部空格或指定字符  
String.prototype.TrimStart = function (c) {
    if (c == null || c == "") {
        var str = this.replace(/^s*/, '');
        return str;
    }
    else {
        var rg = new RegExp("^" + c + "*");
        var str = this.replace(rg, '');
        return str;
    }
}

//去除字符串尾部空格或指定字符  
String.prototype.TrimEnd = function (c) {
    if (c == null || c == "") {
        var str = this;
        var rg = /s/;
        var i = str.length;
        while (rg.test(str.charAt(--i)));
        return str.slice(0, i + 1);
    }
    else {
        var str = this;
        var rg = new RegExp(c);
        var i = str.length;
        while (rg.test(str.charAt(--i)));
        return str.slice(0, i + 1);
    }
}
class DateUtils {
	constructor() {
		Date.prototype.Format = function (fmt) { //author: meizz 
			var o = {
				"M+": this.getMonth() + 1, //月份 
				"d+": this.getDate(), //日 
				"h+": this.getHours(), //小时 
				"m+": this.getMinutes(), //分 
				"s+": this.getSeconds(), //秒 
				"S": this.getMilliseconds() //毫秒 
			};
			if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
			for (var k in o)
				if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
			return fmt;
		}

	}
	/**
	 * [getYestoday 获得昨天的格式化日期]
	 * @return {[type]} [description]
	 */
	getYestoday(formatstr) {
		return new Date(new Date().getTime() - 1000 * 60 * 60 * 24).Format(formatstr)
	}
	getAddDays(days,formatstr) {
		return new Date(new Date().getTime() + 1000 * 60 * 60 * 24*days).Format(formatstr)
	}
	dateAdd( srcDate, intervalType, intervalCount) {
		var dtTmp = srcDate;
		switch (intervalType) {
			case 's': return new Date(Date.parse(dtTmp) + (1000 * intervalCount));
			case 'm': return new Date(Date.parse(dtTmp) + (60 * 1000 * intervalCount));
			case 'H': return new Date(Date.parse(dtTmp) + (60 * 60 * 1000 * intervalCount));
			case 'd': return new Date(Date.parse(dtTmp) + (24 * 60 * 60 * 1000 * intervalCount));
			case 'w': return new Date(Date.parse(dtTmp) + (7 * 24 * 60 * 60 * 1000 * intervalCount));   // 周
			case 'q': return new Date(dtTmp.getFullYear(), dtTmp.getMonth() + intervalCount * 3, dtTmp.getDate(), dtTmp.getHours(), dtTmp.getMinutes(), dtTmp.getSeconds());   // 季度
			case 'M': return new Date(dtTmp.getFullYear(), dtTmp.getMonth() + intervalCount, dtTmp.getDate(), dtTmp.getHours(), dtTmp.getMinutes(), dtTmp.getSeconds());
			case 'y': return new Date(dtTmp.getFullYear() + intervalCount, dtTmp.getMonth(), dtTmp.getDate(), dtTmp.getHours(), dtTmp.getMinutes(), dtTmp.getSeconds());
		}
	}
	/**
	 * [getToday 获得今天的格式化日期]
	 * @return {[type]} [description]
	 */
	getToday(formatstr) {
		return new Date().Format(formatstr)
	}

	/**
	 * [getTomorrow 获得明天的格式化日期]
	 * @return {[type]} [description]
	 */
	getTomorrow(formatstr) {
		return new Date(new Date().getTime() + 1000 * 60 * 60 * 24).Format(formatstr)
	}
	Format(thisday,formatstr){
		return new Date(thisday ).Format(formatstr)
	}
	/**字符串转成日期格式 */
	 stringToDate(dateStr,separator){
		if(!separator){
			   separator="-";
		}
		var dateArr = dateStr.split(separator);
		var year = parseInt(dateArr[0]);
		var month;                     
		if(dateArr[1].indexOf("0") == 0){
			month = parseInt(dateArr[1].substring(1));
		}else{
			 month = parseInt(dateArr[1]);
		}
		var day = parseInt(dateArr[2]);
		var date = new Date(year,month -1,day);
		return date;
	}
}
export default new DateUtils();


