
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'


export default {
components: {
Cell,
Group,
CellBox
},
data() {
return {
list: [],
title: "",
imgUrl: "",
contents: "",
telNo: "",
level: "",
address: "",
line: "",
context:''
};
},
async created() {
let resp = await this.getHosInfo("001");

this.imgUrl = "http://" + resp.hosNetUrl + "/" + resp.imgUrl;
console.log("imgurl=", this.imgUrl);
this.context =decodeURIComponent(resp.contents);
this.contents = this.DelHtml(decodeURIComponent(resp.contents));
this.telNo = resp.telNo;
this.level = resp.level;
this.address = resp.address;
this.line = resp.line;
},
methods: {
DelHtml(description) {
description = description.replace(/(\n)/g, "");
description = description.replace(/(\t)/g, "");
description = description.replace(/(\r)/g, "");
description = description.replace(/<\/?[^>]*>/g, "");
description = description.replace(/\s*/g, "");
return description;
},
async getHosInfo(code) {
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getL2012 } = require("../../static/wxGetData");
let res = await getL2012(code).catch(error => {
throw new Error(error.retMsg);
});

return res;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
}
};



