
import Checker from 'vux/src/components/checker/checker.vue'
import Card from 'vux/src/components/card/index.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'

import { finished } from "stream";
import myUtils from "../utils/myUtils";
import vueQr from "vue-qr";
export default {
components: {
Card,
vueQr,
XButton,
Group,
Cell,
Grid,
GridItem,
XInput,
GroupTitle,
Checker,
CheckerItem,
Tabbar,
TabbarItem,
Qrcode,
CellBox,
Divider,
FlexboxItem,
Flexbox
},
data() {
return {
patientId: "",
headimgurl: "",
nickname: "",
phone: "",
List: [],
userQrcodeValue: " ",
isShowQrcode: false,
imageUrl: require("../style/img/logo_.png")
};
},
methods: {

async camera() {
this.$router.replace({ name: "camera" });
},
toDetail(item) {
this.$store.commit("userInfo", item);
this.$router.push({
name: "sickDetail",
params: {
userInfo: item
}
});
},
async addNewCard() {
this.$router.replace({ name: "sickCreate" });
},
async getCardList() {
let redirect_uri = encodeURIComponent(
"http://weixin.zysyy.com.cn/#/myInfo"
);
let url = `https://health.tengmed.com/open/getHealthCardList?redirect_uri=${redirect_uri} `;
window.location.replace(url);
},

async SaveRegistInfo(reqData) {
let that = this;

try {
/*微信平台获取的信息*/
let userBase = JSON.parse(localStorage.getItem("useBaseInfo")); 
let weixinData = {};
weixinData.openid = this.$store.state.openid;
weixinData.nickname = userBase.nickname;
weixinData.province = userBase.province;
weixinData.city = userBase.city;
weixinData.headimgurl = userBase.headimgurl;
weixinData.country = userBase.country;
reqData.openid = this.$store.state.openid;
reqData.hosCardType = "3";
this.$vux.loading.show({ text: "Loading" }); 
let data = { cardInfo: reqData, wixin: weixinData };
const { getH1007 } = require("../static/wxGetData");
let res = await getH1007(data).catch(error => {
throw new Error(error.retMsg);
});
this.$vux.loading.hide(); 
this.$router.replace({ name: "myInfo" });
} catch (error) {

this.$vux.loading.hide(); 
this.$vux.toast.show({ text: error.message, type: "text" });
} finally {
this.$vux.loading.hide(); 
}
}
},

async created() {
this.$parent.showHead(false);
const { querySickInfo } = require("../static/commReq");

this.List = await querySickInfo(this); //await this.querySickInfo();

if (Array.isArray(this.List) && this.List.length > 0) {
this.isShowQrcode = true;
}

let hCode = myUtils.getUrlKey("healthCode");

if (!myUtils.isNull(hCode)) {
const { getHealthCardList } = require("../static/wxGetData");
let res = await getHealthCardList({ healthCode: hCode }).catch(error => {
this.$vux.toast.show({ text: error.retMsg, type: "text" });
throw new Error(error.retMsg);
});

let card = res.card;
let reqData = {};
reqData.name = card.name;
reqData.gender = card.gender;
reqData.nation = card.nation;
reqData.birth = card.birthday;
reqData.certNo = card.idNumber;
reqData.phone = card.phone1;
reqData.hosCardNo = card.qrCodeText;
if (myUtils.listIsExist(this.List, reqData.hosCardNo, "hosCardNo")) {
this.$vux.toast.show({ text: "该用户已存在", type: "text" });
return;
}

await this.SaveRegistInfo(reqData);

this.List = await querySickInfo(this);
}
},
computed: {
count: function() {
let mycount = 0;
if (Array.isArray(this.List) && this.List.length > 0) {
mycount = this.List.length;
}
return 5 - mycount;
}
}
};



