
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { getH2003 } from "../../static/wxGetData";
import inbaseinfo from "../common/inbaseinfo";
import selectSick from "../common/selectSick";


export default {
data() {
return {
isExist: false,
index: 0,
list: [],
listTab: [
{ itemName: "近一周" },
{ itemName: "三个月" },
{ itemName: "半年内" },
{ itemName: "一年内" }
],
selecedItem: "近一周",
inHosInfo: {
inHosPatientName: "",
sex: "",
certNo: "",
certyType: "",
inHosNo: "",
deptName: "",
bed: "",
prePayTotalAmount: "0",
totalFee: "0",
inHosDate: ""
}
};
},
components: {
selectSick,
XTable,
Tab,
TabItem,
Swiper,
SwiperItem,
XButton,
dateFormat,
inbaseinfo
},
vuex: {},
methods: {
async getInHosDailyFee(dateStart, dateEnd) {
let that = this;
try {
let inHosNo = this.$store.state.inHosInfo.inHosNo;
const { getH2004 } = require("../../static/wxGetData");
let res = await getH2004(inHosNo, dateStart, dateEnd).catch(error => {
throw new Error(error.retMsg);
});

this.list = res;
this.isExist =
this.list == undefined || this.list.length == 0 ? true : false;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({
title: "",
content: error.message,
onHide() {

}
});
} finally {
this.$vux.loading.hide(); 
}
},

async queryRecord() {
let dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
let dateEnd = this.dateUtil.getToday("yyyyMMdd");
await this.getInHosDailyFee(dateStart, dateEnd);
}
},
async mounted() {
this.queryRecord();
},
async created() {
this.inHosInfo = this.$store.state.inHosInfo;
},
computed: {
indexitem() {
return this.index;
}
},
watch: {
indexitem(newData, oldData) {
console.log("oldData, newData", oldData, newData);
let dateStart = "";

let now = new Date();
switch (newData) {
case 0: {
dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
break;
}
case 1: {
dateStart = this.dateUtil.Format(
this.dateUtil.dateAdd(now, "M", -3),
"yyyyMMdd"
);
break;
}
case 2: {
dateStart = this.dateUtil.Format(
this.dateUtil.dateAdd(now, "M", -6),
"yyyyMMdd"
); //dateFormat(new Date().setMonth(-3), "YYYYMMDD");
break;
}
case 3: {
dateStart = this.dateUtil.Format(
this.dateUtil.dateAdd(now, "M", -12),
"yyyyMMdd"
); // dateFormat(new Date().setFullYear(-1), "YYYYMMDD");
break;
}
}

let dateEnd = dateFormat(now, "YYYYMMDD");
this.getInHosDailyFee(dateStart, dateEnd);
}
}
};



