
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'


export default {
data() {
return {
isHidePay: true,
isExist: true,
prescription: {},
user: {},
payAmount: "￥0.00",

itemList: []
};
},
components: {
Cell,
CellBox,
Card,
Group,
XButton,
Flexbox,
FlexboxItem,
FormPreview,
XTable
},
vuex: {},
methods: {
async getUnPayDetail() {
try {
this.$vux.loading.show({ text: "Loading" }); 
let user = JSON.parse(localStorage.getItem("userInfo"));
let hosCardNo =user.hosCardNo;
let hosCardType = user.hosCardType;
let hosCardFrom = user.hosCardFrom;
const { getH1020 } = require("../../static/wxGetData");
let res = await getH1020(
hosCardNo,
hosCardType,
hosCardFrom,
this.prescription.prescriptionNo
).catch(error => {
throw new Error(error.retMsg);
});

this.itemList = res;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async unifiedorder() {
let payUtil = {};
try {
this.$vux.loading.show({ text: "Loading" }); 

let model = {};
model.openid = this.user.openid; 
model.hosCardNo = this.user.hosCardNo;
model.hosCardType = this.user.hosCardType; 
model.hosCardFrom = this.user.hosCardFrom; 
model.patientId = this.user.patientId;
model.sex = this.user.sex == "1" ? "1" : "2";
model.phone = this.user.phone;
model.certNo = this.user.certNo;
model.prescriptionNos = this.prescription.prescriptionNo; 
model.prescriptionsAmount = Number( this.prescription.prescriptionAmount).toFixed(2); 
model.payAmount =Number( this.prescription.prescriptionAmount).toFixed(2); 


model.total_fee = Number( model.prescriptionsAmount).toFixed(2); 
model.busCode = "02";
model.payMode = "0"; 
model.body = "处方缴费";

console.log("model", model);
const { getL5003 } = require("../../static/wxGetData");
let res = await getL5003(model).catch(error => {
throw new Error(error.retMsg);
});

payUtil = res;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
return payUtil;
},
async callUnifiedorder() {
try {
this.isHidePay = false;
let data = await this.unifiedorder();
data.model = "04";
let self = this;
let wexinPay = require("../.././Libs/weixinPay");



wexinPay(
data,
async res => {
self.$router.replace({
name: "outPayResult",
params: {
msg: "支付完成",
payRetunData: data,
pNo: self.prescription.prescriptionNo
}
});

},
res => {
self.$router.replace({
name: "errorPage",
params: { errmsg: result.responseHead.retMsg }
});

}
);
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
}
}
},
mounted() {
this.prescription = this.$route.params.prescription;
this.user =   JSON.parse(localStorage.getItem("userInfo"));
this.payAmount = Number.parseFloat(
this.prescription.prescriptionAmount
).toFixed(2);
this.getUnPayDetail();
},
created() {},
computed: {









},
watch: {}
};



