
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
components: {
Group,
Box,
XTable,
Cell,
CellBox,
Msg,
Card
},

data() {
return {
description: "",
msgTitle: "",
transResult: {},
icon: "",
buttons: [





]
};
},
methods: {
async getCureDetail(receiptNo) {
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getH10341 } = require("../../static/wxGetData");
let res = await getH10341(receiptNo).catch(error => {
throw new Error(error.retMsg);
});

this.itemList = res;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
async created() {
try {
this.$vux.loading.show({ text: "正在通讯中" }); 
let user = JSON.parse(localStorage.getItem("userInfo"));
let data = this.$route.params.payRetunData;
data.name = user.name;
const { getH1021 } = require("../../static/wxGetData");
this.transResult = await getH1021(data).catch(error => {
throw new Error(error.retMsg);
});

let receiptNo = this.transResult.receiptNo.TrimEnd(",");
this.description = ` 缴款单号:${receiptNo}`;
await this.getCureDetail(receiptNo);
this.msgTitle = `缴费成功,本次缴款 <span style='color:red'>${Number.parseFloat(
this.transResult.prescriptionsAmount
).toFixed(2)}</span>元`;

this.$vux.loading.hide(); 
} catch (err) {
this.$vux.loading.hide(); 
this.$router.replace({
name: "errorPage",
params: { errmsg: err.message }
});
} finally {
this.$vux.loading.hide(); 
}
}
};



