import regist from '../components/regist/Regist'
import registIndex from '../components/regist/RegistIndex'
import registDept from '../components/regist/registDept'
import registDoctor from '../components/regist/RegistDoctor'
import registConfirm from '../components/regist/registConfirm'
import registResult from '../components/regist/registResult'



import appoint from '../components/appoint/appoint'
import appointDept from '../components/appoint/appointDept'
import appointDoctor from '../components/appoint/appointDoctor'
import appointConfirm from '../components/appoint/appointConfirm'
import appointResult from '../components/appoint/appointResult'

import cureRecordIndex from '../components/CureRecord/CureRecordIndex'
import cureRecord from '../components/CureRecord/cureRecord'
import cureRecordDetail from '../components/CureRecord/cureRecordDetail'


import inPay from '../components/inPay/inPay'
import inPayConfirm from '../components/inPay/inPayConfirm'
import inPayIndex from '../components/inPay/inPayIndex'
import inPayResult from '../components/inPay/inPayResult'
import inPayQueryInSick from '../components/inPay/inPayQueryInSick'


import inHosDailyFee from '../components/inHosDailyFee/inHosDailyFee'
import inHosDailyFeeIndex from '../components/inHosDailyFee/inHosDailyFeeIndex'
import inHosDailyQueryInSick from '../components/inHosDailyFee/inHosDailyQueryInSick'

import inPayRecord from '../components/inPayRecord/inPayRecord'
import inPayRecordIndex from '../components/inPayRecord/inPayRecordIndex'
import inPayRecordQueryInSick from '../components/inPayRecord/inPayRecordQueryInSick'
import qrcode from '../components/myQrcode/qrcode'

import sick from '../components/sick/sick'
import sickBind from '../components/sick/sickBind'
import sickList from '../components/sick/sickList'
import sickBindSuccess from '../components/sick/sickBindSuccess'
import sickCreate from '../components/sick/sickCreate'
import sickCreate2 from '../components/sick/sickCreate2'
import sickDetail from '../components/sick/sickDetail'
import camera from '../components/sick/camera'


import outPay from '../components/outPay/outPay'
import outPayIndex from '../components/outPay/outPayIndex'
import outPayDetail from '../components/outPay/outPayDetail'
import outPayResult from '../components/outPay/outPayResult'
import outDoctorPrescription from '../components/outPay/outDoctorPrescription'


import reportQuery from '../components/reportQuery/reportQuery'
import reportQueryIndex from '../components/reportQuery/reportQueryIndex'
import reportQueryDetail from '../components/reportQuery/reportQueryDetail'

import registRecord from '../components/registRecord/registRecord'
import registRecordIndex from '../components/registRecord/registRecordIndex'

import appointRegistRecord from '../components/appointRegistRecord/appointRegistRecord'
import appointRegistRecordIndex from '../components/appointRegistRecord/appointRegistRecordIndex'
import appointRegistRecordDetail from '../components/appointRegistRecord/appointRegistRecordDetail'


import hosNetIndex from '../components/hosNet/hosNetIndex'
import hosInfo from '../components/hosNet/hosInfo'
import inHosProcess from '../components/hosNet/inHosProcess'
import insurance from '../components/hosNet/insurance'
import outHosProcess from '../components/hosNet/outHosProcess'
import treatmentProcess from '../components/hosNet/treatmentProcess'
import hosInfoDetail from '../components/hosNet/hosInfoDetail'
import hosMap from '../components/hosNet/hosMap'

import errorPage from '../components/ErrorPage'
import myInfo from '../components/myInfo'
import inHos from '../components/inHos'
import home from '../components/home'
import main from '../components/main'
import modifyInfo from '../components/common/modifyInfo'


const appRouter = [{
  path: '/',
  name: 'home',
  component: home,
  meta: {
    title: '首页',

    checkAuth: 0,
  }
}, {
  path: '/main',
  name: 'main',
  component: main,
  meta: {
    title: '',
    checkAuth: 0,
  }
}, {
  path: '/myInfo',
  name: 'myInfo',
  component: myInfo,
  meta: {
    title: '我的信息',

    checkAuth: 0,
  }
}, {
  path: '/inHos',
  name: 'inHos',
  component: inHos,
  meta: {
    title: '住院首页',
    checkAuth: 0,
  }
}, {
  path: '/modifyInfo',
  name: 'modifyInfo',
  component: modifyInfo,
  meta: {
    title: '修改持卡人信息',
    checkAuth: 0,
  }
},

{
  path: '/sick',
  name: 'sick',
  redirect: '/sickList',
  component: sick,
  children: [
    {
      path: '/sickBind/',
      meta: {
        title: '信息绑定',
        checkAuth: 0,
      },
      name: 'sickBind',
      component: sickBind,
    }, {
      path: '/sickBindSuccess/',
      meta: {
        title: '信息绑定',
        checkAuth: 0,
      },
      name: 'sickBindSuccess',
      component: sickBindSuccess,


    }, {
      path: '/sickList/',
      meta: {
        title: '选择就诊人',
        checkAuth: 1,
      },
      name: 'sickList',
      component: sickList,
    }, {
      path: '/sickCreate/',
      meta: {
        title: '电子健康卡注册',
        checkAuth: 0,
      },
      name: 'sickCreate',
      component: sickCreate,
    }, {
      path: '/sickCreate2/',
      meta: {
        title: '电子健康卡注册',
        checkAuth: 0,
      },
      name: 'sickCreate2',
      component: sickCreate2,
    }, {
      path: '/sickDetail/',
      meta: {
        title: '电子健康卡信息',
        checkAuth: 0,
      },
      name: 'sickDetail',
      component: sickDetail,
    },
    
  ]
},
{
  path: '/regist/',
  meta: {
    title: '当日挂号',
    checkAuth: 0,
  },
  name: 'regist',
  redirect: '/registIndex',
  component: regist,
  children: [
    {
      path: '/registIndex/',
      meta: {
        title: '选择挂号类型',
        checkAuth: 1,
      },
      name: 'registIndex',
      component: registIndex,
    },
    {
      path: '/registDept/',
      meta: {
        title: '选择挂号科室',
        checkAuth: 0,
      },
      name: 'registDept',
      component: registDept,
    },
    {
      path: '/registDoctor/',
      meta: {
        title: '选择医生',
        checkAuth: 0,
      },
      name: 'registDoctor',
      component: registDoctor,
    }, {
      path: '/registConfirm/',
      meta: {
        title: '选择确认支付',
        checkAuth: 0,
      },
      name: 'registConfirm',
      component: registConfirm,
    },
    {
      path: '/registResult/',
      meta: {
        title: '选择确认支付',
        checkAuth: 0,
      },
      name: 'registResult',
      component: registResult,
    },
    {
      path: '/camera/',
      meta: {
        title: '相机测试',
        checkAuth: 0,
      },
      name: 'camera',
      component: camera,
      
    },

  ]
},
{
  path: '/appoint/',
  meta: {
    title: '预约挂号',
    checkAuth: 0,
  },
  name: 'appoint',
  redirect: '/appointDept',
  component: appoint,
  children: [

    {
      path: '/appointDept/',
      meta: {
        title: '预约挂号选择科室',
        checkAuth: 1,
      },
      name: 'appointDept',
      component: appointDept,
    },
    {
      path: '/appointDoctor/',
      meta: {
        title: '预约挂号选择医生',
        checkAuth: 0,
      },
      name: 'appointDoctor',
      component: appointDoctor,
    }, {
      path: '/appointConfirm/',
      meta: {
        title: '预约挂号确认支付',
        checkAuth: 0,
      },
      name: 'appointConfirm',
      component: appointConfirm,
    },
    {
      path: '/appointResult/',
      meta: {
        title: '预约挂号支付结果',
        checkAuth: 0,
      },
      name: 'appointResult',
      component: appointResult,
    },

  ]
},
{
  path: '/outPay/',
  meta: {
    title: '门诊交费',
    checkAuth: 0,
  },
  name: 'outPay',
  component: outPay,
  redirect: '/outPayIndex/',
  children: [{
    path: '/outPayIndex/',
    meta: {
      title: '待缴费处方',
      checkAuth: 1,
    },
    name: 'outPayIndex',
    component: outPayIndex,
  },
  {
    path: '/outPayDetail/',
    meta: {
      title: '处方明细',
      checkAuth: 0,
    },
    name: 'outPayDetail',
    component: outPayDetail,
  },
  {
    path: '/outDoctorPrescription/',
    meta: {
      title: '处方明细',
      checkAuth: 0,
    },
    name: 'outDoctorPrescription',
    component: outDoctorPrescription,
  },
  
  {
    path: '/outPayResult/',
    meta: {
      title: '支付完成',
      checkAuth: 0,
    },
    name: 'outPayResult',
    component: outPayResult,
  },
  ]
},
{
  path: '/inPay/',
  meta: {
    title: '住院交费',
    checkAuth: 0,
  },
  name: 'inPay',
  redirect: '/inPayQueryInSick/',
  component: inPay,
  children: [{
    path: '/inPayIndex/',
    name: 'inPayIndex',
    component: inPayIndex,
    meta: {
      title: '预交金缴费',
      checkAuth: 1,
    }
  },
  {
    path: '/inPayConfirm/',
    name: 'inPayConfirm',
    component: inPayConfirm,
    meta: {
      title: '选择确认支付',
      checkAuth: 0,
    },

  }, {
    path: '/inPayResult/',
    name: 'inPayResult',
    component: inPayResult,
    meta: {
      title: '预交金结果',
      checkAuth: 1,
    },
  }, {
    path: '/inPayQueryInSick/',
    name: 'inPayQueryInSick',
    component: inPayQueryInSick,
    meta: {
      title: '住院预交金',
      checkAuth: 0,
    },
  }
  ]
}, {
  path: '/reportQuery/',
  meta: {
    title: '报告单查询',
    checkAuth: 0,
  },
  name: 'reportQuery',
  redirect: '/reportQueryIndex/',
  component: reportQuery,
  children: [{
    path: '/reportQueryIndex/',
    meta: {
      title: '报告单查询',
      checkAuth: 1,
    },
    name: 'reportQueryIndex',
    component: reportQueryIndex,
  },
  {
    path: '/reportQueryDetail/',
    meta: {
      title: '报告单明细',
      checkAuth: 0,
    },
    name: 'reportQueryDetail',
    component: reportQueryDetail,
  },
  ]
}, {
  path: '/registRecord/',
  meta: {
    title: '挂号记录查询',
    checkAuth: 1,
  },
  name: 'registRecord',
  redirect: '/registRecordIndex/',
  component: registRecord,
  children: [{
    path: '/registRecordIndex/',
    meta: {
      title: '挂号记录查询',
      checkAuth: 1,
    },
    name: 'registRecordIndex',
    component: registRecordIndex,
  }
  ]
}, {
  path: '/appointRegistRecord/',
  meta: {
    title: '预约挂号记录查询',
    checkAuth: 1,
  },
  name: 'appointRegistRecord',
  redirect: '/appointRegistRecordIndex/',
  component: appointRegistRecord,
  children: [{
    path: '/appointRegistRecordIndex/',
    meta: {
      title: '预约挂号记录查询',
      checkAuth: 1,
    },
    name: 'appointRegistRecordIndex',
    component: appointRegistRecordIndex,
  },{
    path: '/appointRegistRecordDetail/',
    meta: {
      title: '预约挂号记录明细',
      checkAuth: 1,
    },
    name: 'appointRegistRecordDetail',
    component: appointRegistRecordDetail,
  }
  ]
}, {
  path: '/qrcode/',
  meta: {
    title: '用户二维码',
    checkAuth: 1,
  },
  name: 'qrcode',
  redirect: '/qrcode/',
  component: qrcode
}, {
  path: '/hosNetIndex/',
  meta: {
    title: '报告单查询',
    checkAuth: 1,
  },
  name: 'hosNetIndex',
  redirect: '/hosInfo/',
  component: hosNetIndex,
  children: [{
    path: '/hosInfo/',
    meta: {
      title: '医院介绍',
      checkAuth: 0,
    },
    name: 'hosInfo',
    component: hosInfo,
  },
  {
    path: '/inHosProcess/',
    meta: {
      title: '住院流程',
      checkAuth: 0,
    },
    name: 'inHosProcess',
    component: inHosProcess,
  },
  {
    path: '/insurance/',
    meta: {
      title: '医保须知',
      checkAuth: 0,
    },
    name: 'insurance',
    component: insurance,
  },
  {
    path: '/hosMap/',
    meta: {
      title: '到院导航',
      checkAuth: 0,
    },
    name: 'hosMap',
    component: hosMap,
  },

  {
    path: '/outHosProcess/',
    meta: {
      title: '出院流程',
      checkAuth: 0,
    },
    name: 'outHosProcess',
    component: outHosProcess,
  },
  {
    path: '/treatmentProcess/',
    meta: {
      title: '就诊流程',
      checkAuth: 0,
    },
    name: 'treatmentProcess',
    component: treatmentProcess,
  }, {
    path: '/hosInfoDetail/',
    meta: {
      title: '详细介绍',
      checkAuth: 0,
    },
    name: 'hosInfoDetail',
    component: hosInfoDetail,
  },

  ]
},


{
  path: '/inHosDailyFee/',
  meta: {
    title: '日清单查询',
    checkAuth: 0,
  },
  name: 'inHosDailyFee',
  redirect: '/inHosDailyQueryInSick/',
  component: inHosDailyFee,
  children: [{
    path: '/inHosDailyFeeIndex/',
    meta: {
      title: '住院日清单查询',
      checkAuth: 0,
    },
    name: 'inHosDailyFeeIndex',
    component: inHosDailyFeeIndex,
  }, {
    path: '/inHosDailyQueryInSick/',
    name: 'inHosDailyQueryInSick',
    component: inHosDailyQueryInSick,
    meta: {
      title: '住院日清单查询',
      checkAuth: 0,
    },

  }

  ]
},
{
  path: '/inPayRecord/',
  meta: {
    title: '预交金记录查询',
    checkAuth: 1,
  },
  name: 'inPayRecord',
  redirect: '/inPayRecordQueryInSick/',
  component: inPayRecord,
  children: [{
    path: '/inPayRecordIndex/',
    meta: {
      title: '预交金记录查询',
      checkAuth: 1,
    },
    name: 'inPayRecordIndex',
    component: inPayRecordIndex,
  }, {
    path: '/inPayRecordQueryInSick/',
    meta: {
      title: '住院预交金记录查询',
      checkAuth: 0,
    },
    name: 'inPayRecordQueryInSick',
    component: inPayRecordQueryInSick,
  }]
},

{
  path: '/cureRecord/',
  redirect: 'cureRecordIndex',
  meta: {
    title: '就诊记录',
    checkAuth: 0,
  },
  name: 'cureRecord',
  component: cureRecord,
  children: [{
    path: '/cureRecordIndex/',
    name: 'cureRecordIndex',
    component: cureRecordIndex,
    meta: {
      title: '就诊记录',
      checkAuth: 1,
    },
  },
  {
    path: '/cureRecordDetail/',
    name: 'cureRecordDetail',
    component: cureRecordDetail,
    meta: {
      title: '就诊记录明细',
      checkAuth: 0,
    },
  },
  ]

},
{
  path: '/errorPage/',

  name: 'errorPage',
  component: errorPage,
  meta: {
    title: '错误提示',
    checkAuth: 0,
  }
}


]
export default appRouter;



