
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Box from 'vux/src/components/box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
components: {
Checker,
CheckerItem,
Box,
Flexbox,
FlexboxItem,
XButton,
GroupTitle,
Group,
CellBox,
dateFormat
},
data() {
return {
items: [],
typeId: 0,
deptId: 0,
deptName: "",
dateCheck: 0,
registerFee: ""
};
},
methods: {
onCellClick(item) {
this.$router.replace({
name: "registConfirm",
params: { regInfo: item, typeId: this.typeId }
});
},
async getDoctor(deptId, typeId) {
let res = [];
try {
let now = new Date();
let hour = now.getHours();
let schedulingDate = dateFormat(now, "YYYYMMDD");






this.$vux.loading.show({ text: "Loading" }); 
const { getH1005 } = require("../../static/wxGetData");

res = await getH1005(deptId, schedulingDate, typeId).catch(error => {

});

res = typeof res == "undefined" ? [] : res;

if (typeId == 1 || typeId == 2) {
let tempDoctor = {
deptId: deptId,
deptName: this.deptName,
doctorId: "",
doctorName: "全科医生",
doctorSex: "01",
doctorNamePinyin: "",
doctorNameWubi: "",
feeList: { feeInfo: { fee: this.registerFee, feeName: "诊疗费" } },
registerTotalFee: this.registerFee,
totalSourceNum: "0",
remainNum: "0",
typeId: "1",
doctorImageUrl: require("../../assets/pic_doctor.png"),
dateList: {
dateInfo: { worktime: "" }
},
doctorTitle: "无"
};
tempDoctor.dateList.dateInfo.schedulingSequenceNum =
hour < 13 ? "1" : "2";
tempDoctor.dateList.dateInfo.worktime = hour < 13 ? "上午" : "下午";
res.push(tempDoctor);
}

this.items = res.filter(item => {
if (
(hour < 13 && item.dateList.dateInfo.worktime == "上午") ||
(hour > 12 && item.dateList.dateInfo.worktime == "下午")
) {
return true;
} else {
return false;
}
});
} catch (error) {
console.log("error", error);
this.$vux.loading.hide();
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
created() {
this.typeId = this.$store.state.typeId;
this.deptName = this.$route.params.itemName;
this.deptId = this.$route.params.itemId;
this.registerFee = this.$route.params.registerFee; 

if (typeof this.deptName == "undefined" || this.deptName == "") {
this.deptName = this.$store.state.deptName;
} else {
this.$store.commit("deptName", this.deptName);
}
if (typeof this.deptId == "undefined" || this.deptId == "") {
this.deptId = this.$store.state.deptId;
} else {
this.$store.commit("deptId", this.deptId);
}
console.log("dept", this.typeId, this.deptId);
this.getDoctor(this.deptId, this.typeId);
}



};



