
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'




export default {
data() {
return {
item
};
},
components: { Group, Cell },
vuex: {},
methods: {},
mounted() {},
created() {
let titem = this.$route.params.item;
if (titem.schedulingSequenceNum == "1") {
titem.schedulingSequenceNum = "上午";
} else {
titem.schedulingSequenceNum = "下午";
}
let flag = "";
switch (titem.flag) {
case "0": {
flag = "预约";
break;
}
case "1": {
flag = "已挂号";
break;
}
case "2": {
flag = "已取消";
break;
}
case "3": {
flag = "爽约";
break;
}
}
titem.flag = flag;
if (titem.advance == "1") {
titem.advance = "未预扣费";
} else {
titem.advance = "已扣费";
}
this.item = titem;
}
};



