
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Box from 'vux/src/components/box/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
components: {
Cell,
CellBox,
CellFormPreview,
Group,
Badge,
Icon,
Box
},
data() {
return {
typeId: 0,
nextPage: "",
items: []
};
},
methods: {
onCellClick(item) {
this.$router.replace({
name: this.nextPage,
params: {
itemId: item.deptId,
itemName: item.deptName,
typeId: this.typeId,
registerFee: item.registerFee
}
});
},
async getDept() {
try {

let schedulingDate = dateFormat(new Date(), "YYYYMMDD");

this.$vux.loading.show({ text: "Loading" }); 
const { getH1004 } = require("../../static/wxGetData");
let res = await getH1004(schedulingDate, this.typeId).catch(error => {
throw new Error(error.retMsg);
});
var hash = {};
let arr = res.reduce(function(item, next) {
hash[next.deptId]
? ""
: (hash[next.deptId] = true && item.push(next));
return item;
}, []);

this.items = arr;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
created() {
this.typeId = this.$route.params.itemId;
if (typeof this.typeId == "undefined" || this.typeId == "") {
this.typeId = this.$store.state.typeId;
} else {
this.$store.commit("typeId", this.typeId);
}

this.nextPage = "registDoctor";
this.getDept();
}
};



