
import myUtils from "../../utils/myUtils";
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Box from 'vux/src/components/box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { log } from "util";

export default {
components: {
PopupHeader,
Popup,
Checker,
CheckerItem,
Box,
Flexbox,
FlexboxItem,
XButton,
GroupTitle,
Group,
CellBox,
dateFormat
},
data() {
return {
demo5: 1,
weekList: [],
items: [],
itemsDoctor: [],
seeFlowList: [],
activeInex: 0,
typeId: 0,
deptId: 0,
deptName: "",
dateCheck: 0,
registerFee: "",
show1: false
};
},
methods: {
onCellClick(item) {
this.$router.replace({
name: "appointConfirm",
params: { regInfo: item, typeId: this.typeId }
});
},
async getDoctor() {
let res = [];
let data = {};
try {
let now = new Date();
data.dateStart = this.dateUtil.getAddDays(1, "yyyyMMdd"); 
data.dateEnd = this.dateUtil.getAddDays(8, "yyyyMMdd"); 
data.deptId = this.$store.state.deptId;
data.typeId = this.$store.state.typeId;

this.$vux.loading.show({ text: "Loading" }); 
const { getH3001 } = require("../../static/wxGetData");

res = await getH3001(data).catch(error => {
throw new Error(error.retMsg);
});

res = typeof res == "undefined" ? [] : res;
this.itemsDoctor = res;
await this.filerData(data.dateStart);
} catch (error) {
this.$vux.loading.hide();
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async filerData(curDate) {
let itemsTemp = this.itemsDoctor.filter(item => {
let srcDate = item.orderDateList.dateInfo.date;

let itemDate = dateFormat(this.stringToDate(srcDate), "YYYYMMDD");

if (itemDate == curDate) {
return true;
} else {
return false;
}
});

this.items = itemsTemp.map(item => {
let dateInfo = item.orderDateList.dateInfo;
let srcDate = dateInfo.date;
item.itemDate = dateFormat(this.stringToDate(srcDate), "YYYY-MM-DD");
item.curDate = dateFormat(this.stringToDate(srcDate), "YYYYMMDD");
item.worktime = dateInfo.worktime;
item.schedulingSequenceNum = dateInfo.schedulingSequenceNum;
return item;
});
},
stringToDate(srcDate) {
let dt = srcDate.split(" ");

let newrq1 = dt[0].split("-");
if (dt.length > 1) {
let newtime = dt[1].split(":");
return new Date(
newrq1[0],
parseInt(newrq1[1]) - 1,
newrq1[2],
newtime[0],
newtime[1],
newtime[2]
);
} else {
return new Date(newrq1[0], parseInt(newrq1[1]) - 1, newrq1[2]);
}
},
async callTransFlow(item) {
this.seeFlowList = await this.getTranFlow(item);

this.show1 = true;
},
/*
查询分时段预约排班医生号源
*/

async getTranFlow(doctInfo) {
this.$store.commit("doctorInfo", doctInfo);

let res = [];
let data = {};
try {
let now = new Date();
data.schedulingDate = doctInfo.curDate; 
data.schedulingSequenceNum = doctInfo.schedulingSequenceNum; 
data.doctorId = doctInfo.doctorId; 
data.deptId = this.$store.state.deptId;
data.typeId = this.$store.state.typeId;

this.$vux.loading.show({ text: "Loading" }); 
const { getH3002 } = require("../../static/wxGetData");

let resRecord = await getH3002(data).catch(error => {
throw new Error(error.retMsg);
});
res = resRecord.map(item => {
console.log(
"this.stringToDate(item.SeeTimeStart",
item.SeeTimeStart,
this.stringToDate(item.SeeTimeStart)
);
item.SeeDate = dateFormat(
this.stringToDate(item.SeeTimeStart),
"YYYY-MM-DD"
);
item.SeeTime = dateFormat(
this.stringToDate(item.SeeTimeStart),
"HH:mm"
);
console.log("items=", item);
return item;
});
} catch (error) {
this.$vux.loading.hide();
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
return res;
},
getWeek() {
for (let i = 1; i < 8; i++) {
let aDataObj = {};
aDataObj.day = this.dateUtil.getAddDays(i, "d");
aDataObj.pbDay = this.dateUtil.getAddDays(i, "yyyyMMdd");
if (i == 1) {
this.demo5 = aDataObj.pbDay;
}
aDataObj.week = myUtils.getWeek(aDataObj.pbDay);
console.log("this.demo5", aDataObj.week);
aDataObj.active = true;

this.weekList.push(aDataObj);
}
console.log("this.demo5", this.weekList);
},
async TransNext(item) {
try {
let dateStart = dateFormat(
this.stringToDate(item.SeeTimeStart),
"YYYYMMDD"
);
let isExist = await this.getHistroyRegistInfo(
dateStart,
item.deptId,
item.doctorId
);
if (isExist) {
this.$router.replace({
name: "appointConfirm",
params: { regInfo: item }
});
} else {
throw new Error("此日期您已经预约过此医生");
}
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
async getHistroyRegistInfo(dateStart, deptId, doctorId) {
let data = {};
let userInfo = JSON.parse(localStorage.getItem("userInfo"));
data.hosCardNo = userInfo.hosCardNo;
data.hosCardFrom = userInfo.hosCardFrom;
data.hosCardType = userInfo.hosCardType;
data.dateStart = dateStart;
data.dateEnd = dateStart;
this.$vux.loading.show({ text: "Loading" }); 
const { getH3004 } = require("../../static/wxGetData");
let res = await getH3004(data).catch(error => {
console.log("error", error);
return [];
});
console.log("res", res);
res.filter(el => {
if (
dateFormat(this.stringToDate(el.orderDate), "YYYYMMDD") ==
dateStart &&
el.deptId == deptId &&
el.doctorId == doctorId
) {
return true;
}
});
return res.length == 0 ? true : false;
},
togglePay(item, index) {
this.activeInex = index;
this.filerData(item.pbDay);
}
},

created() {
this.getWeek();
this.getDoctor();
}



};



