
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import selectSick from "../common/selectSick";

export default {
data() {
return {
isHaveRecord: false,
list: [],
listTab: [
{ itemName: "近一周" },
{ itemName: "三个月" },
{ itemName: "半年内" },
{ itemName: "一年内" }
],
selecedItem: "近一周",
index: 0
};
},
components: {
XTable,
Tab,
TabItem,
Swiper,
SwiperItem,
XButton,
dateFormat,
selectSick
},
vuex: {},
methods: {
async changeUserInfo() {
let now = new Date();
let dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd"); 
let dateEnd = this.dateUtil.getAddDays(30, "yyyyMMdd");
await this.getReport(dateStart, dateEnd);
},
async getReport(dateStart, dateEnd) {
try {
this.list = [];
let data = {};
let userInfo =  JSON.parse(localStorage.getItem("userInfo"))
data.hosCardNo = userInfo.hosCardNo;
data.hosCardFrom = userInfo.hosCardFrom;
data.hosCardType = userInfo.hosCardType;
data.dateStart = dateStart;
data.dateEnd = dateEnd;
data.name=userInfo.name
this.$vux.loading.show({ text: "Loading" }); 
const { getH3004 } = require("../../static/wxGetData");
let res = await getH3004(data).catch(error => {
throw new Error(error.retMsg);
});

this.list = res;
this.isHaveRecord =
this.list == undefined || this.list.length == 0 ? true : false;
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
async beforeCreate() {},
async created() {
this.changeUserInfo();
},
computed: {
indexitem() {
return this.index;
}
},
watch: {
indexitem(newData, oldData) {
console.log("oldData, newData", oldData, newData);
let dateStart = "";

let now = new Date();
switch (newData) {
case 0: {
dateStart = this.dateUtil.getAddDays(-7, "yyyyMMdd");
break;
}
case 1: {
dateStart = this.dateUtil.Format(
this.dateUtil.dateAdd(now, "M", -3),
"yyyyMMdd"
);
break;
}
case 2: {
dateStart = this.dateUtil.Format(
this.dateUtil.dateAdd(now, "M", -6),
"yyyyMMdd"
);
break;
}
case 3: {
dateStart = this.dateUtil.Format(
this.dateUtil.dateAdd(now, "M", -12),
"yyyyMMdd"
);
break;
}
}

let dateEnd = this.dateUtil.getAddDays(30, "yyyyMMdd");
this.getReport(dateStart, dateEnd);
}
}
};



