
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
components: {
Cell,
CellBox,
Msg,
Card
},
data() {
return {
description: " ",
msgTitle: "预约挂号成功",
transResult: {},
icon: "",
amount: 0,
buttons: [





]
};
},
methods: {},







async created() {
try {
this.$vux.loading.show({ text: "正在通讯中" }); 
this.msgTitle = "预约挂号正在进行中";
this.icon = "waiting";
let data = this.$route.params.payRetunData;
const { getH3003 } = require("../../static/wxGetData");

this.transResult = await getH3003(data).catch(error => {
this.icon = "warn";
this.msgTitle = "预约挂号失败";
throw new Error(error.retMsg);
});

this.icon = "success";
this.msgTitle = "预约挂号成功";

this.$vux.loading.hide(); 
} catch (err) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "提示", content: err.message });
} finally {
this.$vux.loading.hide(); 
}
}
};



