
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'

import vueQr from "vue-qr";
import { getA002 } from "../../static/wxGetData";
import myUtils from "../../utils/myUtils";
export default {
components: {
XButton,
Group,
Cell,
vueQr,
Box
},
data() {
return {
hosUser: {},
imageUrl: require("../../assets/healthCard/logo.png"),
certType: "01",
userInfo: { name: "", certNo: "", phone: "", hosCardNo: "" }
};
},
methods: {
async toCardPage() {
let orderId = await this.getOrderId();
let redirect_uri = encodeURIComponent(
"http://weixin.zysyy.com.cn/#/sickDetail"
);
let url = ` https://health.tengmed.com/open/takeMsCard?order_id=${orderId}&redirect_uri=${redirect_uri}`;
console.log("url===", url);
window.location.replace(url);
},
async getOrderId() {
let qrCodeText = this.userInfo.hosCardNo;
const { getOrderIdByOutAppId } = require("../../static/wxGetData");

let resp = await getOrderIdByOutAppId({ qrCodeText: qrCodeText });

return resp.orderId;
},
async onUnBind() {
let that = this;
that.$vux.confirm.show({
title: "确定要解除该用户绑定吗?",


onConfirm: async () => {
const {  onUnBind,querySickInfo } = require("../../static/commReq");
await onUnBind( that.userInfo,that,()=>{
that.$router.replace({ name: "myInfo" });
});

}
});
},
},
async created() {
let myUser = this.$route.params.userInfo; 
console.log('myUser',myUser);
const { querySickInfo, setDefault } = require("../../static/commReq");

if (myUser==undefined) {

await querySickInfo(this);
} else {

await setDefault(myUser, this);

}

this.userInfo =JSON.parse(localStorage.getItem("userInfo"));;

}
};



