
import util from "../../utils/myUtils";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'




export default {
data() {
return {
receiptNo: 0,
itemList: []
};
},
components: { Group, Box, XTable, Tab, TabItem, Swiper, SwiperItem, XButton },
vuex: {},
methods: {
async getCureDetail(receiptNo) {
try {
this.$vux.loading.show({ text: "Loading" }); 
const { getH1034 } = require("../../static/wxGetData");
let res = await getH1034(receiptNo).catch(error => {
throw new Error(error.retMsg);
});

this.itemList = res;
} catch (error) {
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
}
},
created() {
this.receiptNo = this.$route.params.receiptNo;
this.getCureDetail(this.receiptNo);
},
computed: {
indexitem() {
return this.index;
}
},
watch: {
indexitem(oldData, newData) {
console.log(newData, oldData);
console.log();
}
}
};



