export async function querySickInfo(eleObj) {

  let reqData = {};
  let openid = eleObj.$store.state.openid;

  openid = openid != "" ? openid : localStorage.getItem("openid");

  eleObj.$store.commit("openid", openid);
  reqData.openid = eleObj.$store.state.openid;
  const {
    getH1006
  } = require("./wxGetData");
  let res = await getH1006(reqData).catch(error => {
    throw new Error(error.retMsg);
  });

  res.forEach(element => {
    if (element.isDefault == "1") {
      localStorage.setItem("userInfo",JSON.stringify(element));
      eleObj.$store.commit("userInfo", element);
    }
  });
  eleObj.$store.commit("userList", res);
  localStorage.setItem("userList",JSON.stringify(res));
  return res;
}
export async function setDefault(user, eleObj) { //将用户设置成默认值

  let res = {};

  try {
    eleObj.$store.commit("userInfo", user);
    localStorage.setItem("openid", user.openid)
    localStorage.setItem("userInfo",JSON.stringify( user))
    const {
      getH0010
    } = require("./wxGetData");
    res = await getH0010(user).catch(err => {
      console.log('err=', err);
    })


  } catch (error) {
    console.log('出错', err);
  }
  return res;
}
export async function onUnBind(reqData,eleObj,callback) {
 

  try {
    //reqData.openid = eleObj.user.openid;
    eleObj.$vux.loading.show({ text: "Loading" }); // 显示
    const { getH1000 } = require("./wxGetData");
    let res = await getH1000(reqData).catch(error => {
      throw new Error(error.retMsg);
    });
    eleObj.$vux.loading.hide(); // 隐藏
    eleObj.$vux.alert.show({
      title: "",
      content: "解除绑定成功",
      async onHide() {
        callback();
      }
    });
  } catch (error) {
    eleObj.$vux.alert.show({ title: "", content: error.message });
  } finally {
    eleObj.$vux.loading.hide(); // 隐藏
  }
}



