
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Radio from 'vux/src/components/radio/index.vue'


export default {
name: "selectSick",
components: { CellBox, Group, PopupHeader, Popup, FlexboxItem, Flexbox ,Radio},
data() {
return {
isExist: false,
show1: false,
tempUser: "",
wxUserList: [],
curUser: {}
};
},
methods: {
confirm() {
this.curUser = this.tempUser;
this.$store.commit("userInfo", this.curUser);
localStorage.setItem("userInfo",JSON.stringify(this.curUser))
this.setDefault(this.curUser);
this.show1 = false;
this.$emit("changeUserInfo");
},
change(value, label) {
this.$store.state.userList.forEach(element => {
if (element.hosCardNo == value) {
this.tempUser = element;
console.log("curUser=", this.curUser);
}
});
},
async setDefault(reqData) {
let that = this;

try {
this.$vux.loading.show({ text: "Loading" }); 
const { getH0010 } = require("../../static/wxGetData");
let res = await getH0010(reqData).catch(error => {
throw new Error(error.retMsg);
});
} catch (error) {
this.$vux.loading.hide(); 
this.$vux.alert.show({ title: "", content: error.message });
} finally {
this.$vux.loading.hide(); 
}
},
onSelect() {
this.show1 = true;
}
},
async mounted() {
this.curUser =  JSON.parse(localStorage.getItem("userInfo"));

this.wxUserList = this.$store.state.userList.map(item => {
let obj = { key: item.hosCardNo, value: item.name };

return obj;
});
}
};



